
export function title(block) {
    return `<div class="row">
                <div class="col-sm">
                    <h1>${block.value}</h1>
                </div>
            </div>`
}

export function text(block) {
    return `<div class="row">
                <div class="col-sm">
                    <p>${block.value}</p>
                </div>
            </div>`
}

export function columns(block) {
    const html = block.value.map(item => `<div class="col-sm">${item}</div>`)
    console.log(html)
    return `<div class="row">
                ${html.join('')}
            </div>`
}

export function img(block) {
    return `<div class="row"><img width="300" src="${block.value}" alt="img"></div>`
}
